.page-footer {
  background-color: $background-block-color;
}

.page-footer__contacts {
  background-color: $background-block-color;

  &--index {
    background-color: $contrast-color;
  }
}

.page-footer__contacts-wrapper {
  display: flex;
  justify-content: space-between;

  padding-top: 15px;
  padding-bottom: 10px;
}

.page-footer__contacts-title {
  margin-right: 15px;

  text-transform: uppercase;

  color: $header-color;

  font-family: "Oswald", "Arial MT", sans-serif;
  font-size: 16px;
  line-height: 20px;
}

.page-footer__contacts-address {
  line-height: 20px;
}

.page-footer__map-wraper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.page-footer__map {
  position: relative;

  display: none;

  width: 320px;
  height: 360px;
  margin: 0 auto;

  background-image: url("../img/bg-map-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.png");
    background-size: contain;
  }

  &::before {
    position: absolute;
    top: 163px;
    left: 130px;

    width: 55px;
    height: 53px;

    content: "";

    background-image: url("../img/map-pin.png");
    background-size: contain;
  }

  &--no-js {
    display: block;
  }
}

.page-footer__google-map {
  display: block;

  width: 100%;
  padding: 0;

  border: none;

  &--no-js {
    display: none;
  }
}

.page-footer__wraper {
  display: flex;
  align-items: center;
  flex-direction: column;

  padding-top: 25px;
  padding-bottom: 20px;
}

.page-footer__social {
  display: flex;
}

.page-footer__logo {
  width: 100%;
  height: 100%;
  padding-top: 15px;
  padding-bottom: 25px;

  text-align: center;

  border-bottom: 1px solid $line-program-color;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-footer__copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;
  padding-top: 15px;

  color: $content-text-color;

  font-size: 16px;
  line-height: 16px;

  &:hover,
  &:focus {
    .page-footer__copyright-logo {
      color: $btn-color;
    }
  }

  &:active {
    opacity: 0.3;

    .page-footer__copyright-logo {
      color: $btn-color;
    }
  }
}

.page-footer__copyright-logo {
  margin-right: -5px;

  color: $icon-color;
}

@media (min-width: $tablet-width) {
  .page-footer__contacts-wrapper {
    width: 630px;
    padding-top: 45px;
    padding-right: 0;
    padding-bottom: 40px;
    padding-left: 0;
  }

  .page-footer__contacts-title {
    width: 160px;

    font-size: 20px;
    line-height: 25px;
  }

  .page-footer__contacts-address {
    width: 160px;

    text-align: right;
  }

  .page-footer__contacts-city {
    display: block;

    padding-top: 24px;
  }

  .page-footer__map {
    width: 767px;
    height: 400px;

    background-image: url("../img/bg-map-tablet@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.png");
    }

    &::before {
      top: 154px;
      left: 327px;

      width: 113px;
      height: 106px;
    }
  }

  .page-footer__google-map {
    height: 400px;
  }

  .page-footer__wraper {
    align-items: stretch;
    flex-direction: row;
    justify-content: center;

    min-height: 150px;
    padding-bottom: 5px;
  }

  .page-footer__logo {
    padding-top: 40px;

    text-align: left;

    border-bottom: none;
  }

  .page-footer__copyright {
    align-items: flex-start;
    justify-content: flex-end;

    padding-top: 25px;
  }

  .page-footer__copyright-title {
    padding-right: 12px;
  }

  .page-footer__copyright-logo {
    width: 45px;
    height: 45px;
    margin-right: -8px;
    padding-top: 5px;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__map-wraper {
    position: relative;
  }

  .page-footer__map {
    width: 100%;
    max-width: 1440px;

    background-image: url("../img/bg-map-desktop@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.png");
    }

    &::before {
      top: 157px;
      left: 1005px;
    }
  }

  .page-footer__contacts-wrapper {
    position: absolute;
    z-index: 1;
    top: 95px;
    left: 106px;

    width: 570px;
    height: 201px;
    padding-right: 80px;
    padding-left: 80px;

    background-color: $contrast-color;
  }

  .page-footer__contacts-address {
    text-align: left;
  }

  .page-footer__contacts-city {
    padding-top: 20px;
  }
}
